
import './App.css';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { AuthProvider, useAuth } from "./contexts/AuthContext";
import Home from './pages/Home/Home';
import Navigation from './components/Navigation/Navigation';
import Signin from './pages/Signin/Signin';
import CreateLession from './pages/CreateLession/CreateLession';
import TutorialDataService from "./firebaseDB/database";

import { useEffect, useState } from 'react';
import AppUpdate from './pages/AppUpdate/AppUpdate';
import Video from "./pages/Video/Video"
import Quiz from "./pages/Quiz/Quiz"


function App() {



    return (


        <BrowserRouter>
            <Navigation />
            <AuthProvider>
                <Switch>

                    <GuestRoute path="/" exact>
                        <Signin />

                    </GuestRoute>

                    {/* <ProtectedRoute path="/home">
                        <Home />
                    </ProtectedRoute> */}
                    <ProtectedRoute path="/createlession">
                        <CreateLession />
                    </ProtectedRoute>
                    <ProtectedRoute path="/appupdate">
                        <AppUpdate />
                    </ProtectedRoute>
                    <ProtectedRoute path="/quiz">
                        <Quiz />
                    </ProtectedRoute>
                    <ProtectedRoute path="/video">
                        <Video />
                    </ProtectedRoute>
                    <Route>
                        <Signin />
                    </Route>
                </Switch>

            </AuthProvider>
        </BrowserRouter>


    );
}

const GuestRoute = ({ children, ...rest }) => {

    const { currentUser } = useAuth();
    // const { logout } = useAuth();



    const [valid, setValid] = useState(false);

    useEffect(() => {


        TutorialDataService.getAll().on("value", function (snapshot) {

            snapshot.forEach(function (child) {

                if (currentUser.email.replace(".", "%") === child.key) {

                    setValid(true);

                }
            });
        });


    })






    // if (currentUser && !fireDB) {
    //     logout();

    // }


    // let data = {
    //     title: "Title",
    //     description: " this.state.description",
    //     published: false
    // };
    // console.log(data);

    // TutorialDataService.create(data)
    //     .then(() => {
    //         console.log("Created new item successfully!");
    //         // this.setState({
    //         //     submitted: true,
    //         // });
    //     })
    //     .catch((e) => {
    //         console.log(e);
    //     });




    return (
        <Route
            {...rest}
            render={({ location }) => {
                return currentUser && valid ? (

                    < Home />
                ) : (
                    children
                );
            }}
        ></Route >
    );
};


const ProtectedRoute = ({ children, ...rest }) => {

    const { currentUser } = useAuth();
    const [valid, setValid] = useState(false);

    useEffect(() => {


        TutorialDataService.getAll().on("value", function (snapshot) {

            snapshot.forEach(function (child) {

                if (currentUser.email.replace(".", "%") === child.key) {

                    setValid(true);

                }
            });
        });


    })






    // const { logout } = useAuth()
    // if (currentUser && !fireDB) {
    //     logout();
    // }


    return (
        <Route
            {...rest}
            render={({ location }) => {
                return !currentUser || !valid ? (
                    <Signin />
                ) : (
                    children
                );
            }}
        ></Route>
    );
};

export default App;
