import React, { useState } from 'react'
import Textbox from "../../components/Textbox/Textbox"
import Button from "../../components/Button/Button"
import Loading from "../../components/Loading/Loading"

const CreateLession = () => {

    const [loading, setLoading] = useState(true);

    setTimeout(() => {
        setLoading(false)
    }, 3000);

    const [logoI, setLogoI] = useState("");
    const [lessionN, setLessionN] = useState("")
    const [cTime, setCTime] = useState("")
    const [quizMsg1, setQuizMsg1] = useState("")
    const [quizMsg2, setQuizMsg2] = useState("")
    const [quizMsg3, setQuizMsg3] = useState("")
    const [quizMsg4, setQuizMsg4] = useState("")
    function logoIcon(e) {
        setLogoI(e.target.value)
    }
    function lessionName(e) {
        setLessionN(e.target.value)
    }
    function comTime(e) {
        setCTime(e.target.value)
    }
    function quizMSG1(e) {
        setQuizMsg1(e.target.value)
    }
    function quizMSG2(e) {
        setQuizMsg2(e.target.value)
    }
    function quizMSG3(e) {
        setQuizMsg3(e.target.value)
    }
    function quizMSG4(e) {
        setQuizMsg4(e.target.value)
    }










    return (
        <div className="primary-container">
            <Loading title="Loading..." visible={loading} />
            <Textbox onChange={logoIcon} value={logoI} placeholder="Logo Icon" type="text" name="" />
            <Textbox onChange={lessionName} value={lessionN} placeholder="Lession Name" type="text" name="" />
            <Textbox onChange={comTime} value={cTime} placeholder="Completion Time" type="text" name="" />
            <Textbox onChange={quizMSG1} value={quizMsg1} placeholder="Quiz MSG 1" type="text" name="" />
            <Textbox onChange={quizMSG2} value={quizMsg2} placeholder="Quiz MSG 2" type="text" name="" />
            <Textbox onChange={quizMSG3} value={quizMsg3} placeholder="Quiz MSG 3" type="text" name="" />
            <Textbox onChange={quizMSG4} value={quizMsg4} placeholder="Quiz MSG 4" type="text" name="" />
            <br />    <Button text="Submit" />

        </div>
    )
}

export default CreateLession
